import { useMemo } from "react";

import IDOOn from "../IDOOn";
import TotalRaised from "../TotalRaised";
import AmountRaised from "../AmountRaised";
import PercentCompleted from "../PercentCompleted";
import MinAndMaxAllocation from "../MinAndMaxAllocation";
import TokenAllocationNumbersInToken from "../TokenAllocationNumbersInToken";

import { DAY_IN_MS } from "../../utils/constants";
import { IProjectIdProp, ITokenSymbolProp } from "../../utils/interface";
import useGetAvailableTokens from "../../hooks/state/useGetAvailableTokens";
import useGetStartAndEndDate from "../../hooks/state/useGetStartAndEndDate";

const StatsSection = (props: ITokenSymbolProp & IProjectIdProp) => {
  const { tokenSymbol, id } = props;

  const availableTokens = useGetAvailableTokens(tokenSymbol, id);
  const startAndEndDate = useGetStartAndEndDate(tokenSymbol, id);

  const status: string = useMemo(() => {
    if (startAndEndDate.startDate * 1000 > Date.now()) return "upcoming"

    if (Date.now() < startAndEndDate.endDate * 1000 && availableTokens.value.gt(0)) {
      if (startAndEndDate.endDate * 1000 - Date.now() <= DAY_IN_MS)
        return "24hrsLeft";
      else
        return "live";
    }

    if (availableTokens.value.eq(0)) return "filled";

    return "closed";
  }, [startAndEndDate, availableTokens]);

  if (status === "upcoming") {
    return (
      <div>
        <TotalRaised id={id} tokenSymbol={tokenSymbol} />
        <MinAndMaxAllocation id={id} tokenSymbol={tokenSymbol} />
        <IDOOn id={id} tokenSymbol={tokenSymbol} format={'MMM Do YYYY, h:mm A'} />
      </div>
    )
  }

  if (status === "24hrsLeft" || status === "live") {
    return (
      <div>
        <div className={"m-b-16"}>
          <AmountRaised id={id} tokenSymbol={tokenSymbol} />
          <PercentCompleted id={id} tokenSymbol={tokenSymbol} />
          <TokenAllocationNumbersInToken id={id} tokenSymbol={tokenSymbol} />
        </div>
        <TotalRaised id={id} tokenSymbol={tokenSymbol} />
      </div>
    );
  }

  if (status === "filled") {
    return (
      <div>
        <div className={"m-b-16"}>
          <AmountRaised id={id} tokenSymbol={tokenSymbol} />
          <PercentCompleted id={id} tokenSymbol={tokenSymbol} />
          <TokenAllocationNumbersInToken id={id} tokenSymbol={tokenSymbol} />
        </div>
        <TotalRaised id={id} tokenSymbol={tokenSymbol} />
      </div>
    );
  }

  return (
    <div>
      <div className={"m-b-16"}>
        <AmountRaised id={id} tokenSymbol={tokenSymbol} />
        <PercentCompleted id={id} tokenSymbol={tokenSymbol} />
        <TokenAllocationNumbersInToken id={id} tokenSymbol={tokenSymbol} />
      </div>
      <TotalRaised id={id} tokenSymbol={tokenSymbol} />
    </div>
  );
}

export default StatsSection;
